html {
  height: 100%;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
ul[class],
ol[class] {
  padding: 0;
}
body,
h1,
h2,
h3,
h4,
p,
ul[class],
ol[class],
li,
figure,
figcaption,
blockquote,
dl,
dd {
  margin: 0;
}
body {
  height: 100%;
  scroll-behavior: smooth;
  text-rendering: optimizeSpeed;
  line-height: 1.5;
  background-color: #32363a;
}
ul[class],
ol[class] {
  list-style: none;
}
a:not([class]) {
  -webkit-text-decoration-skip: ink;
          text-decoration-skip-ink: auto;
}
img {
  max-width: 100%;
  display: block;
}
article > * + * {
  margin-top: 1em;
}
input,
button,
textarea,
select {
  font: inherit;
}
@media (prefers-reduced-motion: reduce) {
  * {
    -webkit-animation-duration: 0.01ms !important;
            animation-duration: 0.01ms !important;
    -webkit-animation-iteration-count: 1 !important;
            animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}
#root {
  height: 100%;
}

.post {
  border-bottom: 1px solid grey;
  margin: 0 0 20px 0;
}
.post-header {
  display: flex;
  height: 80px;
  margin: 0 0 20px 0;
}
.post-content {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.post-title {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.title-avatar {
  display: inline-block;
  margin: 0 30px 0 0;
  object-fit: contain;
}
.title-avatar img {
  object-fit: cover;
  height: 80px;
  width: 80px;
  border-radius: 35px;
}
.author-info {
  display: flex;
  align-items: center;
}
.author-nameSername {
  font-size: 20px;
  color: #fff;
  margin: 0 20px 0 0;
}
.author-nickname {
  font-size: 16px;
  color: gray;
}
.post-date {
  font-size: 16px;
  color: #ced4da;
}
.post-text {
  font-size: 18px;
  color: #e9ecef;
}
.post-image {
  object-fit: cover;
  margin: 0 0 20px 0;
}
.post-image img {
  margin: 0 auto;
  border-radius: 35px;
}
.post-footer {
  display: flex;
  padding: 0 0 10px 0;
  justify-content: space-evenly;
  color: #e9ecef;
}
.post-element {
  display: flex;
  align-items: center;
}
.footer-image {
  margin: 0 10px 0 0;
}

.container {
  margin: 0 auto;
  padding: 30px 50px;
  max-width: 1024px;
  height: 100%;
  background-color: #272b30;
}

